import React, { Component } from 'react';
import './tag-control.sass'

export default class TagControlComponent extends Component {
  constructor (props) {
    super(props);
    this.onRemoveBtnClick = this.onRemoveBtnClick.bind(this);
  }

  render () {
    return (
      <span className="tag-control">
        <span className="tag-control__content">{this.props.tag}</span>
        <button className="tag-control__remove" title="Remove" onClick={this.onRemoveBtnClick}>
          x
        </button>
      </span>
    );
  }

  onRemoveBtnClick (e) {
    e.preventDefault();
    this.props.onRemove(this.props.tag);
  }
}