import * as types from './actionTypes';

const initalState = {
  isNoteFormOpened: false,
  searchedTags: []
};

export default function reduce(state = initalState, action = {}) {
  const newState = { ...state };
  switch (action.type) {
    case types.NOTE_FORM_OPEN:
      newState.isNoteFormOpened = true;
      return newState;
    case types.NOTE_FORM_CLOSE:
      newState.isNoteFormOpened = false;
      return newState;
    default:
      return state;
  }
}

export function isNoteFormOpened (state) {
  return state.app.isNoteFormOpened;
}