import React, { Component } from 'react';
import './note.sass';

export default class Note extends Component {
  constructor (props) {
    super(props);
    this.onEditBtnClick = this.onEditBtnClick.bind(this);
    this.onDeleteBtnClick = this.onDeleteBtnClick.bind(this);
  }

  render () {
    let unfocusClass = '';
    let foundedTags = [];
    if (this.props.searchTags.length) {
      unfocusClass = 'note--unfocus';
      foundedTags = this.props.searchTags.filter(searchTag => this.props.note.tags.includes(searchTag));
      if (foundedTags.length) unfocusClass = '';
    }
    return (
      <div className={`note ${unfocusClass} note--color-` + (this.props.note.colorScheme)}>
        <h4 className="note__title">{this.props.note.title}</h4>
        <p className="note__body">{this.props.note.body}</p>
        <div className="note__tags">
          {this.props.note.tags.map((tag, index) => (
            <span key={index} className="tag">{tag}</span>
          ))}
        </div>
        <div className="note__footer">
          <button className="btn btn--sm" onClick={this.onEditBtnClick}>Edit</button>
          <button className="btn btn--sm" onClick={this.onDeleteBtnClick}>Delete</button>
        </div>
      </div>
    );
  }

  onEditBtnClick (e) {
    e.preventDefault();
    this.props.onEdit(this.props.note);
  }

  onDeleteBtnClick (e) {
    e.preventDefault();
    this.props.onDelete(this.props.note);
  }
}