export const ID_COUNTER_KEY = 'NotesIDCounter';
export const NOTES_STORAGE_KEY = 'NotesRecords';

export const ERROR_ID_ALREADY_EXISTS = 'ID already exists';
export const ERROR_NOTE_NOT_EXISTS = 'Note has not exists';

export class NotesService {
  constructor (storage) {
    this.storage = storage;
  }

  get (id) {
    return this.fetchNotes()[id] || null;
  }

  add (note) {
    const id = this.fetchNewID();
    const notes = this.fetchNotes();
    if (notes[id]) throw new Error(ERROR_ID_ALREADY_EXISTS);
    note.id = id;
    notes[id] = note;
    this.saveNotes(notes);
    return note; // returns mutated note
  }

  update (id, data) {
    data = { ...data }; // do not mutate data
    const notes = this.fetchNotes();
    const note = notes[id];
    if (!note) throw new Error(ERROR_NOTE_NOT_EXISTS);
    if (data.id) delete data.id;
    const updatedNote = { ...note, ...data };
    notes[id] = updatedNote;
    this.saveNotes(notes);
    return updatedNote; // returns new object
  }

  delete (note) {
    const notes = this.fetchNotes();
    delete notes[note.id];
    this.saveNotes(notes);
    return note;
  }

  fetchNewID () {
    let curId = this.fetchIDCounter();
    this.saveIDCounter(++curId);
    return curId;
  }

  fetchIDCounter () {
    return this.storage.getItem(ID_COUNTER_KEY) || 0;
  }

  saveIDCounter (num) {
    this.storage.setItem(ID_COUNTER_KEY, num);
  }

  fetchNotes () {
    const notesJSON = this.storage.getItem(NOTES_STORAGE_KEY);
    return notesJSON ? JSON.parse(notesJSON) : {};
  }

  saveNotes (notes) {
    this.storage.setItem(NOTES_STORAGE_KEY, JSON.stringify(notes));
  }
}

export default new NotesService(window ? window.localStorage : null);