import * as types from './actionTypes';

const initalState = {
  note: {
    title: '',
    body: '',
    tags: [],
    colorScheme: 'yellow'
  },
  isOpened: false
}

export default function reduce (state = initalState, action = {}) {
  const newState = { ...state };
  switch (action.type) {
    case types.FORM_OPEN:
      if (action.note) {
        newState.note = { ...action.note };
      }
      newState.isOpened = true;
      return newState;
    case types.FORM_CLOSE:
      newState.note = { ...initalState.note };
      newState.isOpened = false;
      return newState;
    case types.NOTE_CHANGE:
      const data = action.data
      newState.note = { ...newState.note, ...data }
      return newState;
    default:
      return state;
  }
}

export function getNote (state) {
  return state.noteForm.note;
}

export function getIsOpened (state) {
  return state.noteForm.isOpened
}