import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as noteFormSelectors from '../store/noteForm/reducer';

import * as noteFormActions from '../store/noteForm/actions';
import * as appActions from '../store/app/actions';
import * as notesActions from '../store/notes/actions';

import TagsInput from './TagsInputContainer.js';
import ColorSelect from './ColorSelectContainer';
import NoteForm from '../components/NoteForm/NoteFormComponent.js';

class NoteFormContainer extends Component {
  constructor (props) {
    super(props);
    this.onTagsChanged = this.onTagsChanged.bind(this);
    this.onColorChanged = this.onColorChanged.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  render () {
    const tagsInput = <TagsInput tags={this.props.note.tags} onChanged={this.onTagsChanged} />;
    const colorSelect = <ColorSelect selected={this.props.note.colorScheme} 
      onChanged={this.onColorChanged} />
    const noteForm = this.props.isOpened ? <NoteForm 
      note={this.props.note}
      tagsInput={tagsInput} colorSelect={colorSelect} 
      onSubmit={this.onSubmit} /> : '';
    return noteForm;
  }

  onTagsChanged (tags) {
    this.props.dispatch(noteFormActions.changeNote({ tags }));
  }

  onColorChanged (color) {
    this.props.dispatch(noteFormActions.changeNote({ colorScheme: color }));
  }

  onSubmit (data) {
    const note = { ...this.props.note, ...data };
    if (note.id)
      this.props.dispatch(notesActions.updateNote(note.id, note));
    else
      this.props.dispatch(notesActions.addNote(note));
    this.props.dispatch(noteFormActions.close());
    this.props.dispatch(appActions.closeNoteForm());
  }
}

function mapStateToProps (state) {
  return {
    note: noteFormSelectors.getNote(state),
    isOpened: noteFormSelectors.getIsOpened(state)
  }
}

export default connect(mapStateToProps)(NoteFormContainer);