import React, { Component } from 'react';
import './color-option.sass';

export default class ColorOptionComponent extends Component {
  constructor (props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  render () {
    const colorClass = 'color-option--' + this.props.color;
    const selectedClass = this.props.isSelected ? 'color-option--selected' : '';
    return (
      <button className={`color-option ${colorClass} ${selectedClass} color-select__option`}
        onClick={this.onClick} type="button" title={this.props.color}>
        <span className="color-option__label">{this.props.color}</span>
      </button>
    );
  }

  onClick (e) {
    e.preventDefault();
    this.props.onSelect(this.props.color);
  }
}