import React, { Component } from 'react';
import ColorSelect from '../components/ColorSelect/ColorSelectComponent';

export default class ColorSelectContainer extends Component {
  constructor () {
    super()
    this.state = {
      colors: ['yellow', 'blue', 'green']
    };
    this.onChange = this.onChange.bind(this);
  }

  render () {
    return (
      <ColorSelect colors={this.state.colors} selected={this.props.selected || 'yellow'} 
        onChange={this.onChange} />
    );    
  }

  onChange (color) {
    this.setState({ selected: color });
    this.props.onChanged(color);
  }
}