import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as notesSelectors from '../store/notes/reducer';
import * as notesActions from '../store/notes/actions';
import * as appActions from '../store/app/actions';
import * as noteFormActions from '../store/noteForm/actions';

import NotesBoard from '../components/NotesBoard/NotesBoardComponent';

class NotesBoardContainer extends Component {
  constructor (props) {
    super(props);
    this.onNoteEdit = this.onNoteEdit.bind(this);
    this.onNoteDelete = this.onNoteDelete.bind(this);
  }

  componentDidMount () {
    this.props.dispatch(notesActions.fetchNotes());
  }

  render () {
    return (
      <NotesBoard notes={this.props.notes} searchTags={this.props.searchTags}
        onNoteEdit={this.onNoteEdit} onNoteDelete={this.onNoteDelete} />
    );
  }

  onNoteEdit (note) {
    this.props.dispatch(appActions.openNoteForm());
    this.props.dispatch(noteFormActions.open(note));
  }

  onNoteDelete (note) {
    this.props.dispatch(notesActions.deleteNote(note));
  }
}

function mapStateToProps (state) {
  return {
    notes: notesSelectors.getNotesArray(state),
    searchTags: notesSelectors.getSearchTags(state)
  };
}

export default connect(mapStateToProps)(NotesBoardContainer);