import React, { Component } from 'react';
import './app.sass';

export default class App extends Component {
  constructor (props) {
    super(props);
    this.onAddNoteBtnClick = this.onAddNoteBtnClick.bind(this);
    this.onOverlayClick = this.onOverlayClick.bind(this);
    this.onSearchTagsChanged = this.onSearchTagsChanged.bind(this);
  }

  render () {
    return (
      <div className="app">
        <div className="app__header">
          <div className="navbar">
            <h1 className="navbar__brand">Sticky Notes</h1>
            <button className="btn btn--lg" type="button" onClick={this.onAddNoteBtnClick}>
              + Add note
            </button>
            <input className="input-ghost search-tags-input" placeholder="Search tags"
              onChange={this.onSearchTagsChanged} />
          </div>
        </div>
        <div className="app__main">
          {this.props.notesBoard}
        </div>
        <div className={'overlay' + (this.props.isNoteFormOpened ? ' overlay--opened' : '')}
          onClick={this.onOverlayClick}>
          <div className="overlay__content">{this.props.noteForm}</div>
        </div>
      </div>
    );
  }

  onAddNoteBtnClick (e) {
    e.preventDefault();
    this.props.onNoteFormOpen();
  }

  onOverlayClick (e) {
    if (e.currentTarget === e.target) {
      this.props.onOverlayClose();
    }
  }

  onSearchTagsChanged (e) {
    let tags;
    if (e.target.value.trim() === '') tags = [];
    else tags = e.target.value.split(' ');
    this.props.onSearchTagsChanged(tags);
  }
}