import React, { Component } from 'react';
import './notes-board.sass';

import Note from '../Note/NoteComponent';

export default class NotesBoardComponent extends Component {
  render () {
    return (
      <div className="notes-board">
        {this.props.notes.map((note, index) => (
          <div key={index} className="notes-board__item">
            <Note note={note} searchTags={this.props.searchTags}
              onEdit={this.props.onNoteEdit} onDelete={this.props.onNoteDelete} />
          </div>
        ))}
      </div>
    );
  }
}