import React, { Component } from 'react';
import './note-form.sass';

export default class NoteFormComponent extends Component {
  constructor (props) {
    super(props);
    this.onBodyChanged = this.onBodyChanged.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.title = React.createRef();
    this.body = React.createRef();
  }

  render () {
    return(
      <form className='note-form' onSubmit={this.onSubmit}>
        <div className="note-form__block">
          <input type="text" name="title" className="input-ghost input-ghost--xl" 
            defaultValue={this.props.note.title} ref={this.title}
            autoComplete="off" placeholder="Title" />
        </div>
        <div className="note-form__block">
          <textarea name="body" className="textarea-ghost textarea-ghost--lg note-form__body"
            defaultValue={this.props.note.body} ref={this.body} onChange={this.onBodyChanged}
            placeholder="Note text" required></textarea>
        </div>
        <div className="note-form__footer">
          <div className="note-form__tags">{this.props.tagsInput}</div>
          <div className="note-form__colors">{this.props.colorSelect}</div>
          <div className="note-form__buttons">
            <button className="btn btn--lg" type="submit">
              {this.props.note.id ? 'Save note' : 'Add note'}
            </button>
          </div>
        </div>
      </form>
    );
  }

  onBodyChanged (e) {
    const el = e.target;
    const offset = el.offsetHeight - el.clientHeight;
    el.style.height = 'auto';
    el.style.height = el.scrollHeight + offset + 'px';
  }

  onSubmit (e) {
    e.preventDefault();
    this.props.onSubmit({
      title: this.title.current.value,
      body: this.body.current.value
    });
  }
}