import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as appActions from '../store/app/actions';
import * as appSelectors from '../store/app/reducer';

import * as noteFormActions from '../store/noteForm/actions';
import * as notesActions from '../store/notes/actions';

import App from '../components/App/AppComponent';
import NotesBoard from './NotesBoardContainer';
import NoteForm from './NoteFormContainer';

class AppContainer extends Component {
  constructor (props) {
    super(props);
    this.onNoteFormOpen = this.onNoteFormOpen.bind(this);
    this.onNoteFormClose = this.onNoteFormClose.bind(this);
    this.onSearchTagsChanged = this.onSearchTagsChanged.bind(this);
  }

  render () {
    const notesBoard = <NotesBoard />
    const noteForm = <NoteForm />
    return (
      <App notesBoard={notesBoard} 
        noteForm={noteForm} isNoteFormOpened={this.props.isNoteFormOpened} 
        onNoteFormOpen={this.onNoteFormOpen} onOverlayClose={this.onNoteFormClose}
        onSearchTagsChanged={this.onSearchTagsChanged} />
    );
  }

  onNoteFormOpen () {
    this.props.dispatch(appActions.openNoteForm());
    this.props.dispatch(noteFormActions.open());
  }

  onNoteFormClose () {
    this.props.dispatch(appActions.closeNoteForm());
    this.props.dispatch(noteFormActions.close());
  }

  onSearchTagsChanged (tags) {
    this.props.dispatch(notesActions.changeSearchTags(tags));
  }
}

function mapStateToProps (state) {
  return {
    isNoteFormOpened: appSelectors.isNoteFormOpened(state)
  }
}

export default connect(mapStateToProps)(AppContainer);