import * as types from './actionTypes';

const initalState = {
  notesById: {},
  searchTags: []
};

export default function reduce (state = initalState, action = {}) {
  const newState = { ...state };
  switch (action.type) {
    case types.NOTES_FETCHED:
      newState.notesById = { ...action.notes };
      return newState;
    case types.NOTE_ADDED:
      const note = { ...action.note };
      newState.notesById = { ...newState.notesById, [action.note.id]: note };
      return newState;
    case types.NOTE_UPDATED:
      newState.notesById = { ...newState.notesById, [action.note.id]: action.note };
      return newState;
    case types.NOTE_DELETED:
      const notes = { ...newState.notesById };
      delete notes[action.note.id];
      newState.notesById = notes;
      return newState;
    case types.SEARCH_TAGS_CHANGED:
      newState.searchTags = action.tags;
      return newState;
    default:
      return state;
  }
}

export function getNotesArray (state) {
  return Object.values(state.notes.notesById);
}

export function getSearchTags (state) {
  return state.notes.searchTags;
}