import React, { Component } from 'react';
import TagControl from '../TagControl/TagControlComponent';
import './tags-input.sass';

export default class TagsInputComponent extends Component {
  constructor (props) {
    super(props);
    this.onAddBtnClick = this.onAddBtnClick.bind(this);
    this.onInputFocus = this.onInputFocus.bind(this);
    this.onInputBlur = this.onInputBlur.bind(this);
    this.onEnterPressInInput = this.onEnterPressInInput.bind(this);
    this.inputItem = React.createRef();
    this.input = React.createRef();
  }

  render () {
    return (
      <div className="tags-input">
        {this.props.tags.map((tag, index) => (
          <div key={index} className="tags-input__item">
            <TagControl tag={tag} onRemove={this.props.onTagRemove} />
          </div>
        ))}
        <div className="tags-input__item tags-input__item--hidden" ref={this.inputItem}>
          <input type="text" className="tags-input__input" placeholder="new tag"
            ref={this.input} onFocus={this.onInputFocus} onBlur={this.onInputBlur} />
        </div>
        <div className="tags-input__item">
          <button className="btn btn--sm" type="button" onClick={this.onAddBtnClick}>+ add tag</button>
        </div>
      </div>
    );
  }

  showInput () {
    this.inputItem.current.classList.remove('tags-input__item--hidden');
  }

  hideInput () {
    this.inputItem.current.classList.add('tags-input__item--hidden');
  }

  focusInput () {
    this.input.current.focus();
  }

  blurInput () {
    this.input.current.blur();
  }

  startListenEnter () {
    this.input.current.addEventListener('keypress', this.onEnterPressInInput);
  }

  stopListenEnter () {
    this.input.current.removeEventListener('keypress', this.onEnterPressInInput);
  }

  onAddBtnClick (e) {
    e.preventDefault();
    this.showInput();
    this.focusInput();
  }

  onInputFocus (e) {
    this.startListenEnter();
  }

  onInputBlur (e) {
    this.stopListenEnter();
    const value = this.input.current.value.trim();
    if (value !== '') {
      this.props.onTagAdd(value);
      this.input.current.value = '';
    }
    this.hideInput();
  }

  onEnterPressInInput (e) {
    if (e.keyCode === 13) {
      e.preventDefault();
      this.blurInput();
    }
  }
};