import React, { Component } from 'react';
import TagsInput from '../components/TagsInput/TagsInputComponent';

export default class TagsInputContainer extends Component {
  constructor (props) {
    super(props);
    this.state = { tags: this.props.tags };
    this.onTagAdd = this.onTagAdd.bind(this);
    this.onTagRemove = this.onTagRemove.bind(this);
  }

  render () {
    return (
      <TagsInput tags={this.state.tags} onTagAdd={this.onTagAdd} onTagRemove={this.onTagRemove} />
    );
  }

  onTagAdd (tag) {
    const tags = this.state.tags;
    if (!tags.includes(tag)) {
      tags.push(tag);
      this.setState({ tags });
    }
    this.props.onChanged(tags);
  }

  onTagRemove (removed) {
    const tags = this.state.tags.filter(tag => tag !== removed);
    this.setState({ tags: tags });
    this.props.onChanged(tags);
  }
}