import React, { Component } from 'react';
import ColorOption from './ColorOptionComponent';
import './color-select.sass';

export default class ColorSelectComponent extends Component {
  constructor (props) {
    super(props);
    this.onColorSelect = this.onColorSelect.bind(this);
  }

  render () {
    return (
      <div className="color-select">
        {this.props.colors.map((color, index) => (
          <ColorOption key={index} color={color} 
            isSelected={color === this.props.selected} onSelect={this.onColorSelect} />
        ))}
      </div>
    );
  }

  onColorSelect (color) {
    this.props.onChange(color);
  }
}