import * as types from './actionTypes';
import notesService from '../../services/notesService';

export function fetchNotes () {
  return (dispatch) => {
    const notes = notesService.fetchNotes();
    dispatch({ type: types.NOTES_FETCHED, notes });
  }
}

export function addNote (note) {
  return (dispatch) => {
    notesService.add(note);
    dispatch({ type: types.NOTE_ADDED, note });
  };
}

export function updateNote (id, note) {
  return (dispatch) => {
    notesService.update(id, note);
    dispatch({ type: types.NOTE_UPDATED, note });
  };
}

export function deleteNote (note) {
  return (dispatch) => {
    notesService.delete(note);
    dispatch({ type: types.NOTE_DELETED, note });
  };
}

export function changeSearchTags (tags) {
  return { type: types.SEARCH_TAGS_CHANGED, tags };
}
